import React from 'react';
import MyPhoto from './img/myphoto.jpg';
import { StyledProfile, ImageProfile, Img } from './Profile.styled';

const Profile = () => {
    return(
        <StyledProfile>
            <ImageProfile>
            <Img
                alt=""
                src={MyPhoto}
            />
            </ImageProfile>
        </StyledProfile>
    )
}


export default Profile;