import React from 'react';
import { bool, func } from 'prop-types';
import { DivContainer, ButtonSeeMore} from './SeeMore.styled';

const SeeMore = ({ open, setOpen }) => {
    return(
        <DivContainer open={open} onClick={() => setOpen(!open)}>
            <ButtonSeeMore>
            <span>View Portfolio</span>
            </ButtonSeeMore>
        </DivContainer>
        
    )
}

SeeMore.propTypes = {
    open: bool.isRequired,
    setOpen: func.isRequired,
};

export default SeeMore;