import React from 'react';
import { bool, func } from 'prop-types';
import { MyPhoto, Git, Java, Reactjs, Bootstrap, PHP, ASPNet } from '../../img';
import { StyledNav, Closebtn, DivContent, Img, StyledAboutSection, StyledResumeSection, TechImg} from './Content.styled';
import { Grid, GridItem } from '../../GridComp';

const Content = ({ open, setOpen }) => {
  return (
    <DivContent open={open}>
      {/*NAVIGATION BAR*/}
      <StyledNav id="nav-wrap">
        <Closebtn onClick={() => setOpen(!open)}/>
      </StyledNav>

      {/* ABOUT SECTION*/}
      <StyledAboutSection id="about">
        <Grid gutter={50}>
          <GridItem media={{ phone: 1, tablet: 2 / 2 }} col={ 1 / 4}>
              <Img
                  alt=""
                  src={MyPhoto}
              />
          </GridItem>
          <GridItem media={{ phone: 1, tablet: 2 / 2 }} col={ 2 / 4}>
            <h2>About me</h2>
            <p>I am an athlete and a casual gamer, with a passion for coding. Having graduated from my year in college, I pursued my first development job to kick off my career. I was hired as a Software Developer by my former company HiDok Inc., where I work as a full-stack developer.  I strive to attain a good work-life balance to help minimize lapses in productivity, and improve enthusiasm, and generally make work more enjoyable.</p>
            <h2>Contact Details</h2>
            <p>
            <span>Vince Patrick Alferez</span><br/>
            <span>Carnation Street</span><br/>
            <span>Buhangin, Davao City, 8000</span><br/>
            <span>0947 967 7592</span><br/>
            <span className="mail"><a href="mailto:vincepatrickalferez@gmail.com" target="_top">vincepatrickalferez@gmail.com</a></span><br/>
            </p>
          </GridItem>
        </Grid>
      </StyledAboutSection>

      {/*RESUME SECTION*/}
      <StyledResumeSection id="resume">
        {/*EDUCATION*/}
      <Grid gutter={50}>
        <GridItem media={{phone: 1, tablet: 2 / 2}} col={ 1 / 4}>
          <h1><span>Education</span></h1>
        </GridItem>
          <GridItem media={{phone: 1, tablet: 2 / 2}} col={ 3 / 4}>
            <h3>University of Southeastern Philippines</h3>
            <p className="info">Bachelor of Science in Computer Science
            <span>{' '}{' '}</span>
            <em className="date">June 2013 - June 2018</em>
            </p>
            <p>I moved to Davao City after I graduated from High School. Here I took my Bachelor of Science in Computer Science and discovered the underlying theory, concepts, and science behind computer programming. Here I built a Graphical Information System that lets users report an incident in their area. Also, an Internet of Things (IoT) that helps students view the seat occupancy of the library using their smartphones. My roles were to provide a user-friendly User Interface while handling the written document.</p>
            <h3>Holy Cross of Bansalan College</h3>
            <p className="info">
            <em className="date">June 2009 - June 2013</em>
            </p>
            <p>I attended my high school at Holy Cross of Bansalan (HCBC) as it is close to home. While here, there was not much internet exposure so I joined the Badminton team during my Freshman and Sophomore year.  I joined the Sepak Takraw team during my Junior year but was not qualified to play competitively till my Senior year where we qualified for inter-school and managed to bring home a bronze trophy with a big smile.</p>
          </GridItem>
      </Grid>
        {/*WORK*/}
      <Grid gutter={50}>
        <GridItem media={{phone: 1, tablet: 2 / 2}} col={ 1 / 4}>
          <h1><span>Work</span></h1>
        </GridItem>
        <GridItem media={{phone: 1, tablet: 2 / 2}} col={ 3 / 4}>
          <h3>HiDok Inc.</h3>
          <p className="info">Software Developer
          <span>{' '}{' '}</span>
          <em className="date">August 2018 - May 2019</em>
          </p>
          <p>For most of my time at HiDok Inc., I have worked as a Full Stack Software Developer. Here I have had the chance to kick start my career as a Software Developer and get my hands dirty with all parts of software development.</p>
          <h3>University of Southeastern Philippines</h3>
          <p className="info">Software Developer Intern
          <span>{' '}{' '}</span>
          <em className="date">June 2017 - August 2017</em>
          </p>
          <p>We were incorporated by the University to develop a Graphical Information System that lets users report an incident in their area.</p>
        </GridItem>
      </Grid>
        {/*SKILLS*/}
      <Grid gutter={50}>
        <GridItem media={{phone: 1, tablet: 2 / 2}} col={ 1 / 4}>
          <h1><span>Technical Skills</span></h1>
        </GridItem>
        <GridItem media={{phone: 1, tablet: 2 / 2}} col={ 3 / 4}>
          <p className="skillsDesc">I'm fairly new to the software development industry but I'm always interested in playing around with new technologies and applied its full potential.</p>
        </GridItem>
      </Grid>
        {/*LIST OF SKILLS*/}
      <Grid gutter={50}>
        <GridItem media={{phone: 1, tablet: 1 / 2}} col={ 1 / 4}>
        <TechImg
            alt="java"
            src={Java}
          />
          <h5>Java</h5>
          <p className="techDesc">
          This is the first programming language that I started using in my college years. It is by far the language that I am most familiar with as I have used it to build a custom compiler for my school project.
          </p>
        </GridItem>
        <GridItem media={{phone: 1, tablet: 1 / 2}} col={ 1 / 4}>
        <TechImg
            alt="reactjs"
            src={Reactjs}
          />
          <h5>React JS</h5>
          <p className="techDesc">
          I'm reasonably new to React and still familiarizing it by building some projects like this portfolio as my start point. I will continue to use it going forward as I'm sure my knowledge with it will improve over time.
          </p>
        </GridItem>
        <GridItem media={{phone: 1, tablet: 1 / 2}} col={ 1 / 4}>
          <TechImg
            alt="git"
            src={Git}
          />
          <h5>Git</h5>
          <p className="techDesc">
          I have learned about Git right around the time as I started learning ReactJS. I have used it for version control in my ReactJS project.
          </p>
        </GridItem>
      </Grid>
      <Grid gutter={50}>
      <GridItem media={{phone: 1, tablet: 1 / 2}} col={ 1 / 4}>
          <TechImg
            alt="php"
            src={PHP}  
          />
          <h5>PHP</h5>
          <p className="techDesc">
          PHP is the first Web Development programming language that I used back in my college years. It is my starting point as a web developer and learned all the basic knowledge needed for web development.
          </p>
        </GridItem>
        <GridItem media={{phone: 1, tablet: 1 / 2}} col={ 1 / 4}>
          <TechImg
            alt="bootstrap"
            src={Bootstrap}
          />
          <h5>Bootstrap</h5>
          <p className="techDesc">
          I use Bootstrap more often than not when designing the layout of my sites. It is the first framework that I used together with PHP when designing the sites as it is quicker than others.
          </p>
        </GridItem>
        <GridItem media={{phone: 1, tablet: 1 / 2}} col={ 1 / 4}>
          <TechImg 
            alt="aspnet"
            src={ASPNet}
          />
          <h5>ASP.Net</h5>
          <p className="techDesc">
          ASP.Net was fairly new to me back when I began working for HiDok. It has grown on me though and learned a lot as every task that was given involves different functions.
          </p>
        </GridItem>
      </Grid>
      </StyledResumeSection>
    </DivContent>
  )
}
Content.propTypes = {
  open: bool.isRequired,
  setOpen: func.isRequired,
}
export default Content;