import React from 'react';
import { SocialIcon } from 'react-social-icons';
import { StyledIcons } from './SMContact.styled';
import { bool } from 'prop-types';

const SMContact = ({ open }) => {
    return (
        <StyledIcons open={open}>
            <span className="icon-size"><SocialIcon url="https://www.facebook.com/valferez30" target="_blank" network="facebook" fgColor="#fff" style={{height: 40, width: 40}}/></span>
            <span><SocialIcon url="https://www.linkedin.com/in/vince-patrick-alferez-80095b167/" target="_blank" network="linkedin" fgColor="#fff" style={{height: 40, width: 40}}/></span>
            <span><SocialIcon url="https://github.com/valferez" target="_blank" network="github" fgColor="#fff" style={{height: 40,width: 40}}/></span>
        </StyledIcons>
    )
}
SMContact.prototypes = {
    open: bool.isRequired,
}
export default SMContact;